import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import { Link } from 'react-router-dom';

class Create extends Component {

    constructor() {
        super();
        this.state = {
            _id_:'',
            name: '',
            score: '',
            email: ''
        };
    }
    onChange = (e) => {
        const state = this.state
        state[e.target.name] = e.target.value;
        this.setState(state);
    }

    onSubmit = (e) => {
        e.preventDefault();

        const { _id_, name, score, email } = this.state;

        axios.post('/accounts', { _id_, name, score, email })
            .then((result) => {
                this.props.history.push("/")
            });
    }

    render() {
        const { _id_, name, score, email } = this.state;
        return (
            <div class="container">
                <div class="panel panel-default">
                    <div class="panel-heading">
                        <h3 class="panel-title">
                            ADD ACCOUNT
            </h3>
                    </div>
                    <div class="panel-body">
                        <h4><Link to="/"><span class="glyphicon glyphicon-th-list" aria-hidden="true"></span> Account List</Link></h4>
                        <form onSubmit={this.onSubmit}>
                            <div class="form-group">
                                <label for="isbn">Id:</label>
                                <input type="text" class="form-control" name="Id" value={_id_} onChange={this.onChange} placeholder="Id" />
                            </div>
                            <div class="form-group">
                                <label for="isbn">Name:</label>
                                <input type="text" class="form-control" name="name" value={name} onChange={this.onChange} placeholder="Name" />
                            </div>
                            <div class="form-group">
                                <label for="title">Score:</label>
                                <input type="text" class="form-control" name="score" value={score} onChange={this.onChange} placeholder="Score" />
                            </div>
                            <div class="form-group">
                                <label for="publisher">Email:</label>
                                <input type="email" class="form-control" name="email" value={email} onChange={this.onChange} placeholder="Email Address" />
                            </div>
                            <button type="submit" class="btn btn-default">Submit</button>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}

export default Create;
